import Imagem from "@/app/components/Imagem";
import heroImg from "../../assets/bg-image-hero.png";


const SectionOne = () => {
    return (
    <>
    <div className="relative h-screen w-full overflow-hidden">
      <Imagem
        src={heroImg}
        alt="Hero Image"
      />
    </div>
    </>
    )
}
export default SectionOne;